.selecting-gesture {
    width: 100%;

    &__wrapper {
        max-width: 750px;
        width: 100%;
        margin: 0 auto;
        display: flex;
        align-items: center;
        justify-content: space-between;

        @media(max-width: 767px){
            flex-direction: column;
        }
    }

    & span {
        font-weight: 600;
        font-size: 200px;
        line-height: 273px;
        color: $black;

        @media(max-width: 767px){
            font-size: 80px;
            line-height: 80px;
        }
    }

    &__answers {
        @include list-reset;
        display: grid;
        margin-left: 20px;
        grid-template-columns: repeat(3, minmax(90px, 110px));
        grid-gap: 1px;
        background: #F4F4F4;
        box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.08);
        border-radius: 8px;
        overflow: hidden;

        @media(max-width: 767px){
            margin-left: 0;
            margin-top: 40px;
        }

        & li {
            background-color: white;
        }

        & a {
            min-height: 120px;
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            text-decoration: none;

            &:hover {
                opacity: 0.7;
                transition: opacity 0.2s ease;
            }
        }
    }
}