.settings {
    width: 100%;
    
    &__wrapper {
        width: 100%;
        background: $white;
        box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.08);
        border-radius: 8px;
        padding: 0;
        max-width: 600px;
        margin: 0 auto 0  auto;
    }

    &__section:not(:last-child) {
        padding: 0 60px 6px 60px;
        border-bottom: 1px solid $background;

        @media(max-width: 767px){
            padding: 0 20px 6px 20px;
        }
    }

    &__section:last-child {
        padding: 0 60px 16px 60px;

        @media(max-width: 767px){
            padding: 0 20px 16px 20px;
        }
    }

    & p {
        font-weight: 600;
        font-size: 16px;
        line-height: 19px;
        color: #202126;
        margin-top: 27px;
    }

    & label {
        padding-left: 60px;
        font-weight: normal;
        font-size: 14px;
        line-height: 16px;
        display: block;
        color: #505050;
        margin-bottom: 20px;
        position: relative;

        &::before {
            content:"";
            position: absolute;
            width: 16px;
            height: 16px;
            border: 1px solid $black;
            border-radius: 50%;
            top: 0;
            left: 10px;
        }
    }

    & input[type=radio]:checked + label {
        &::after {
            content:"";
            position: absolute;
            width: 10px;
            height: 10px;
            background-color: $black;
            border-radius: 50%;
            top: 3px;
            left: 13px;
        }
    }

    & input[type=range] {
        width: 100%;
        border: none;
        -webkit-appearance: none;
    }

    & input[type=range]:focus {
        outline: none;
    }

    & input[type=range]::-webkit-slider-runnable-track {
        width: 100%;
        height: 2px;
        cursor: pointer;
        background: $black;
        border-radius: 14px;
        border: none;
    }

    & input[type=range]::-webkit-slider-thumb {
        border: 1px solid $white;
        height: 19px;
        width: 19px;
        border-radius: 50%;
        background: #202126;
        cursor: pointer;
        -webkit-appearance: none;
        margin-top: -9px;
    }

    & input[type=range]:focus::-webkit-slider-runnable-track {
        background: $black;
    }

    & input[type=range]::-moz-range-track {
        width: 100%;
        height: 2px;
        cursor: pointer;
        background: $black;
        border-radius: 14px;
        border: none;
    }

    & input[type=range]::-moz-range-thumb {
        border: 1px solid $white;
        height: 19px;
        width: 19px;
        border-radius: 50%;
        background: #202126;
        cursor: pointer;
        margin-top: -9px;
    }

    &__range-minmax {
        width: 100%;
        display: flex;
        justify-content: space-between;
        margin-top: 22px;
        margin-bottom: 17px;

        & span {
            font-weight: normal;
            font-size: 14px;
            line-height: 16px;
            color: #8890A5;
        }
    }
    
}