.education {
    width: 100%;

    &__wrapper {
        max-width: 600px;
        width: 100%;
        margin: 0 auto;
    }

    & ul {
        @include list-reset;
    }

    & li {
        margin-bottom: 26px;
        background: #FFFFFF;
        box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.08);
        border-radius: 8px;
        overflow: hidden;
    }
}