.translate-number {
    width: 100%;

    &__wrapper {
        max-width: 600px;
        width: 100%;
        margin: 0 auto;
        display: flex;
        align-items: center;
        justify-content: space-between;

        @media(max-width: 767px){
            flex-direction: column;
            justify-content: center;
        }
    }

    & span {
        font-weight: normal;
        font-size: 12px;
        line-height: 16px;
        color: $black;
    }

    &__answers {
        @include list-reset;
        display: grid;
        margin-left: 20px;
        grid-template-columns: repeat(3, minmax(90px, 104px));
        grid-gap: 1px;
        background: #F4F4F4;
        box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.08);
        border-radius: 8px;
        overflow: hidden;

        @media(max-width: 767px){
            margin-left: 0;
            margin-top: 40px;
        }

        & li {
            background-color: white;
        }

        & a {
            min-height: 50px;
            width: 100%;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            text-decoration: none;
            padding: 0 15px;

            &:hover {
                opacity: 0.7;
                transition: opacity 0.2s ease;
            }
        }
    }
}