.gesture {
    width: 100%;

    * {
        outline: none;
    }

    &__wrapper {
        max-width: 600px;
        width: 100%;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
    }

    &__slider {
        @include list-reset;
        width: 100%;
        background: #FFFFFF;
        box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.12);
        border-radius: 8px;

        & .slick-slide {
            min-height: 500px;
            display: flex;
            justify-content: center;
            align-items: center;
            padding-bottom: 50px;

            @media(max-width: 450px){
                min-height: 450px;
            }
        }

        & .slick-dots {
            bottom: 20px;
        }

        & .slick-dots li button:before {
            font-size: 36px;
            color: $green;
        }
        & .slick-dots li.slick-active button:before {
            font-size: 45px;
            color: #0BA39D;
        }
    }

    &__word {
        width: 100%;
        background: #FFFFFF;
        box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.12);
        border-radius: 8px;
        margin-bottom: 25px;
        min-height: 66px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 5px 25px;
    }

    & p {
        margin: 0;
    }

    & span {
        display: block;
        font-size: 16px;
        line-height: 22px;

        &:first-child {
            font-weight: 600;
            color: #202126;
        }

        &:last-child {
            font-weight: 500;
            color: #B4B6BC;
        }
    }

    & img {
        margin-top: 3px;
    }
}