.content {
    flex-grow: 1;
    display: flex;
    align-items: center;
    padding: 60px 0;

    &--withheader {
        background: linear-gradient(0deg, #32E0B0 -9.38%, #059B9A 112.03%);
        border-radius: 0 0 8px 8px;
        margin-top: -120px;
    }

    @media(max-width: 767px){
        padding: 40px 0;
    }
}