.footer {
	width: 100%;
	background: $white;
	padding: 20px 15px;
	position: relative;
	z-index: 11;

	@media(max-width: 767px){
		padding: 10px 15px;
   }

	&__wrapper {
		max-width: 510px;
		margin: 0 auto;
	}

	&__menu {
		@include list-reset;
		display: flex;
		justify-content: space-between;

		& li {
			position: relative;
		}

		& a {
			display: flex;
			flex-direction: column;
			align-items: center;
			text-decoration: none;

			&:hover {
				opacity: 0.8;
			}
		}

		& span {
			font-weight: 500;
			margin-top: 8px;
			font-size: 14px;
			line-height: 19px;
			color: $gray;
			min-width: 65px;
			text-align: center;
		}
	}

	&__menu-icon-active {
		display: none;
	}

	&__item--active {
		& span {
			color: $green;
		}

		.footer__menu-icon {
			display: none;
		}

		.footer__menu-icon-active {
			display: block;
		}
	}

	&__submenu {
		display: none;
		position: absolute;
		background: $white;
		width: 220px;
		min-height: 220px;
		right: 33px;
		bottom: 100px;
		border-radius: 20px 20px 0 20px;
		@include list-reset;
		padding-top: 40px;
		padding-bottom: 10px;

		&::after {
			content: "";
			width: 20px;
			height: 20px;
			border: 20px solid transparent; border-top: 30px solid $white; border-right: 50px solid $white;
			position: absolute;
			bottom: -12px;
			right: 0;
		}

		& a {
			font-weight: normal;
			font-size: 14px;
			line-height: 320%;
			margin-bottom: 0px;
		}
	}
}
