.media{
    width: 100%;
    padding: 0 15px;

    &__wrapper {
        width: 100%;
        box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.08);
        border-radius: 8px;
        max-width: 941px;
        margin: 60px auto;
        display: grid;
        grid-gap: 1px;
        grid-template-columns: repeat(3, 1fr);
        overflow: hidden;

        @media(max-width: 767px){
            grid-template-columns: repeat(1, 1fr);
        }
    }

    & a {
        background: $white;
        display: flex;
        justify-content: space-between;
        align-items: center;
        min-height: 60px;
        padding: 20px;
        text-decoration: none;
        font-weight: 600;
        font-size: 14px;
        line-height: 19px;
        color: #202126;

        &:hover {
            opacity: 0.7;
            transition: opacity 0.2s ease;
        }
    }
}