.check-gesture {
    width: 100%;

    &__wrapper {
        max-width: 750px;
        width: 100%;
        margin: 0 auto;
    }

    &__answer {
        display: flex;
        align-items: center;
        justify-content: space-between;

        @media(max-width: 767px){
            flex-direction: column;
        }
    }

    &__char {
        font-weight: 600;
        font-size: 200px;
        line-height: 273px;
        color: $black;
        margin-left: 20px;

        @media(max-width: 767px){
            margin-left: 0;
            font-size: 80px;
            line-height: 80px;
            margin-top: 0px;
            &:first-of-type {
                margin-top: 30px;
            }
        }
    }

    &__check {
        max-width: 313px;
        margin: 100px auto 0 auto;
        display: grid;
        grid-template-columns: repeat(2, minmax(80px, 156px));
        grid-gap: 1px;
        box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.08);
        border-radius: 8px;
        overflow: hidden;

        @media(max-width: 767px){
            margin-top: 40px;
        }

        & a {
            background: $white;
            text-decoration: none;
            min-height: 50px;
            display: flex;
            align-items: center;
            justify-content: center;
            font-weight: normal;
            font-size: 14px;
            line-height: 19px;
            color: $black;

            &:hover {
                opacity: 0.7;
                transition: opacity 0.2s ease;
            }
        }
    }
}