.result {
    width: 100%;
    background: $white;
    box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.08);
    border-radius: 8px;
    padding: 24px 15px;
    max-width: 600px;
    margin: 60px auto 0  auto;
    display: flex;
    justify-content: center;

    @media(max-width: 767px){
        margin-top: 40px;
        padding: 20px 10px;
    }

    & p {
        font-weight: 600;
        font-size: 14px;
        line-height: 19px;
        margin: 0;
        margin-left: 50px;

        @media(max-width: 767px){
            margin-left: 15px;
            font-size: 13px;
            line-height: 16px;
        }

        &:first-child {
            margin-left: 0;
        }
    }
}