.header {
     width: 100%;
     padding: 38px 15px;
     background: $green;

     &--nobackground {
          background: transparent;
     }

     @media(max-width: 767px){
          padding: 25px 25px;
     }

     &__wrapper {
          max-width: 1255px;
          margin: 0 auto;
          position: relative;
     }

     &__back-link {
          position: absolute;
          left: 0;
          top: 50%;
          transform: translateY(-50%);
     }

     & h1 {
          text-align: center;
          font-weight: bold;
          font-size: 32px;
          line-height: 44px;
          color: $light-grey;
          padding: 0 20px;
          margin: 0;

          @media(max-width: 767px){
               font-size: 22px;
               line-height: 30px;
          }
     }
}
