.phrasebook {
    width: 100%;

    &__wrapper{
        max-width: 1255px;
        width: 100%;
        margin: 0 auto;
    }

    &__list-wrapper {
        background: #F4F4F4;
        box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.08);
        border-radius: 8px;
        overflow: hidden;
    }

    & h2 {
        font-weight: 600;
        font-size: 12px;
        line-height: 16px;
        color: #202126;
        margin: 0;
    }

    &__title {
        width: 100%;
        max-width: 600px;
        padding: 25px;
        background: $white;
        box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.08);
        border-radius: 8px;
        margin-bottom: 24px;
    }

    & ul {
        @include list-reset;
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(144px, 1fr));
        grid-gap: 1px;

        & li {
            background: white;
        }

        & a {
            display: flex;
            width: 100%;
            height: 100%;
            padding: 10px;
            min-height: 156px;
            flex-direction: column;
            text-decoration: none;

            &:hover {
                opacity: 0.7;
                transition: opacity 0.2s ease;
            }
        }

        & div {
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            padding-bottom: 12px;
            height: 72px;
        }

        & span {
            font-weight: 500;
            font-size: 10px;
            line-height: 14px;
            color: #B4B6BC;
        }

        & p {
            font-weight: 600;
            font-size: 12px;
            line-height: 16px;
            color: #202126;
            margin-top: 0;
            margin-bottom: 8px;
        }
    }
}