.card {
    display: flex;
    min-height: 100px;
    color: $text;
    text-decoration: none;

    @media(max-width: 767px){
        min-height: 80px;
    }

    &:hover {
        opacity: 0.8;
        transition: opacity 0.2s ease;
    }

    & .card__image-block {
        flex-shrink: 0;
        width: 100px;
        display: flex;
        justify-content: center;
        align-items: center;

        @media(max-width: 767px){
            width: 60px;
        }

        &--yellow {
            background-color: $yellow;
        }
        &--pink {
            background-color: $pink;
        }

        &--blue {
            background-color: $blue;
        }

        &--orange {
            background-color: $orange;
        }
        &--middle-blue {
            background-color: $middle-blue;
        }

        &--skyblue {
            background-color: $skyblue;
        }

        &--purple {
            background-color: $purple;
        }
    }

    & .card__text-block {
        flex-grow: 1;
        padding-left: 24px;
        align-self: stretch;
        display: flex;
        flex-direction: column;
        justify-content: center;

        @media(max-width: 767px){
            padding-left: 15px;
        }

        & span {
            font-weight: 600;
            font-size: 18px;
            line-height: 25px;
            margin-bottom: 0;

            @media(max-width: 767px){
                font-size: 16px;
                line-height: 22px;
            }
        }
    }
}
