.about {
    width: 100%;

    &__wrapper {
        width: 100%;
        max-width: 1154px;
        margin: 0 auto;
        box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.08);
        border-radius: 8px;
        padding: 40px 15px;
        background: $white;
    }

    & ul {
        max-width: 919px;
        list-style: none;
        margin: 0 auto;
        padding: 0;
    }

    & li {
        padding-left: 20px;
        position: relative;
        margin-bottom: 20px;

        &::before {
            content:"";
            position: absolute;
            width: 10px;
            height: 10px;
            background-color: $black;
            border-radius: 50%;
            left: 0;
            top: 5px;
        }
    }

    & p {
        font-weight: 400;
        font-size: 14px;
        line-height: 18px;
        color: $text-gray;
        margin-bottom: 17px;
        margin-top: 0;
    }

    & b {
        color: $black;
        font-weight: 600;
        font-size: 16px;
        line-height: 21px;
    }
}