.validate {
    width: 100%;

    &__wrapper {
        max-width: 1255px;
        width: 100%;
        margin: 0 auto;
    }

    & ul {
        @include list-reset;
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
        grid-gap: 0 60px;

        @media(max-width: 890px) {
            grid-template-columns: 1fr;
            grid-gap: 0;
        }
    }

    & li {
        margin-bottom: 26px;
        background: #FFFFFF;
        box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.08);
        border-radius: 8px;
        overflow: hidden;
        max-width: 600px;

        @media(max-width: 890px) {
            max-width: 100%;
        }

        @media(max-width: 767px) {
            &:last-child {
                margin-bottom: 0;
            }
        }
    }

    & h2 {
        font-weight: 600;
        font-size: 12px;
        line-height: 16px;
        color: #202126;
        margin: 0;
    }

    &__title {
        width: 100%;
        max-width: 47.5%;
        padding: 25px;
        background: $white;
        box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.08);
        border-radius: 8px;
        margin-bottom: 60px;

        @media(max-width: 890px) {
            max-width: 100%;
        }

        @media(max-width: 767px) {
            margin-bottom: 40px;
        }
    }
}