.choosing-pair {
    width: 100%;

    &__wrapper {
        max-width: 332px;
        width: 100%;
        margin: 0 auto;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    & span {
        font-weight: 600;
        font-size: 40px;
        line-height: 55px;
        color: $black;
    }

    &__answers {
        width: 100%;
        @include list-reset;
        display: grid;
        grid-template-columns: repeat(3, minmax(90px, 110px));
        grid-gap: 1px;
        background: #F4F4F4;
        box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.08);
        border-radius: 8px;
        overflow: hidden;
    
        & li {
            background-color: white;
        }
    
        & a {
            min-height: 120px;
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            text-decoration: none;
    
            &:hover {
                opacity: 0.7;
                transition: opacity 0.2s ease;
            }
        }
    }
}
