.alphabet {
    width: 100%;

    &__wrapper{
        max-width: 1154px;
        width: 100%;
        margin: 0 auto;
        background: #F4F4F4;
        box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.08);
        border-radius: 8px;
        overflow: hidden;
    }

    & ul {
        @include list-reset;
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(104px, 1fr));
        grid-gap: 1px;

        @media(max-width: 767px){
            grid-template-columns: repeat(auto-fill, minmax(90px, 1fr));
        }

        & li {
            background: white;
        }

        & a {
            display: flex;
            width: 100%;
            height: 100%;
            min-height: 115px;
            flex-direction: column;
            align-items: center;
            text-decoration: none;

            &:hover {
                opacity: 0.7;
                transition: opacity 0.2s ease;
            }

            @media(max-width: 767px){
                min-height: 100px;
            }
        }

        & div {
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-grow: 1;
        }

        & span {
            font-weight: bold;
            font-size: 22px;
            line-height: 30px;
            color: $text;

            @media(max-width: 767px){
                font-size: 16px;
                line-height: 22px;
            }
        }
    }
}