.translate-word {
    width: 100%;

    &__wrapper {
        max-width: 600px;
        width: 100%;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
    }

    &__enter-block {
        margin-top: 100px;

        @media(max-width: 767px){
            margin-top: 40px;
        }
    }

    &__enter-block form {
        display: flex;
        background: $white;
        box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.08);
        border-radius: 8px;
        overflow: hidden;

        & div {
            padding: 15px 33px;
            border-right: 1px solid $background;
            max-width: 213px;
        }

        & input {
            border: none;
            font-family: $font;
            letter-spacing: 5px;
            text-align: center;
            font-weight: normal;
            font-size: 16px;
            line-height: 22px;
            color: #000000;
            width: 100%;

            &:focus {
                outline: none;
            }
            &:active {
                outline: none;
            }
        }

        & button {
            min-width: 100px;
            font-size: 12px;
            line-height: 16px;
            border: none;
            background: transparent;
            box-shadow: none;
            padding: 0;
            color: #000000;
            font-family: $font;
            letter-spacing: 0.5px;
            cursor: pointer;

            &:hover {
                opacity: 0.7;
                transition: opacity 0.2s ease;
            }

            &:focus {
                outline: none;
            }
            &:active {
                outline: none;
            }
        }
    }
}